'use client'

import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { validateEmail, validatePassword, validateUsername } from '@strife/common';
import { useRouter } from 'next/navigation';
import { ChangeEvent, Fragment, useState } from 'react';
import '../../src/services/account.service';
import { AccountServiceServiceId } from '../../src/services/account.service.interface';
import di from '../di';
import WindowPage from '../../components/WindowPage';

export default function RegisterPage() {
  const router = useRouter();

  const [ email, setEmail ] = useState('');
  const [ emailError, setEmailError ] = useState<string | undefined>(undefined);
  function onChangeEmail(event: ChangeEvent<HTMLInputElement>): void {
    const email = event.target.value;
    setEmail(email)
    const error = email.length == 0 
      ? undefined
      : validateEmail(email);
    setEmailError(error ? error.message : undefined);
  }

  const [ username, setUsername ] = useState('');
  const [ usernameError, setUsernameError ] = useState<string | undefined>(undefined);
  function onChangeUsername(event: ChangeEvent<HTMLInputElement>): void {
    const username = event.target.value;
    setUsername(username)
    const error = username.length == 0
      ? undefined
      : validateUsername(username);
    setUsernameError(error ? error.message : undefined);
  }

  const [ password, setPassword ] = useState('');
  const [ passwordError, setPasswordError ] = useState<string | undefined>(undefined);
  function onChangePassword(event: ChangeEvent<HTMLInputElement>): void {
    const password = event.target.value;
    setPassword(password);
    const error = password.length == 0
      ? undefined
      : validatePassword(password);
    setPasswordError(error ? error.message : undefined);
  }

  const [ error, setError ] = useState<string | undefined>(undefined);

  async function onSubmitRegister(event: React.FormEvent<HTMLFormElement>): Promise<void> {
    event.preventDefault();

    if (emailError || usernameError || passwordError)
      return;

    const accountService = await di.getAsync(AccountServiceServiceId);

    try {
      const account = await accountService.createAccount({ email, username, password });
      onRegisterSuccess();
    } catch (error: any) {
      console.log(error);
      setError(error);
    }
  }

  function onRegisterSuccess(): void {
      setError(undefined);
      setEmail('');
      setUsername('');
      setPassword('');

      router.push('/home');
  }

  return (
    <WindowPage>
      <form className="flex flex-col gap-[32px] p-8" onSubmit={onSubmitRegister}>
        <Typography variant="h2" align="center" fontWeight={700}>strife</Typography>
        <div className="flex flex-col gap-[8px]">
          {
            error == undefined
              ? <Fragment />
              : <Alert variant="filled" severity="error">{error}</Alert>
          }
          <TextField name="email" type="email" value={email} onChange={onChangeEmail} error={emailError != undefined} helperText={emailError} placeholder="email" />
          <TextField name="username" type="text" value={username} onChange={onChangeUsername} error={usernameError != undefined} helperText={usernameError} placeholder="username" />
          <TextField name="password" type="password" value={password} onChange={onChangePassword} error={passwordError != undefined} helperText={passwordError} placeholder="password" />
          <Button type="submit">Register</Button>
          <Link href="/login" className="text-center" fontSize={'small'}>Already have an account?</Link>
        </div>
      </form>
    </WindowPage>
  );
}
